import React, { Component } from 'react';

import './SplashScreen.css';

// import { ReactComponent as IconClose } from '../../assets/images/icons/icon-close.svg';
// import iconClose from '../../assets/images/icons/icon-close.svg';

import { CloseIcon } from '../SVGS';

export default class SplashScreen extends Component {
  constructor(props) {
    super(props);
    this.state = {

    };
  }

  render() {
    if (this.props.isLoaded) {
      this.splashScreenStyle = { opacity: 0, zIndex: 0 };
    }
    return (
      <div
        className="splash-screen"
        style={this.splashScreenStyle}
        onClick={this.props.closeSplashScreen}
      >
        Splash Screen
        <div
          className="splash-screen-close-button button"
        >
          <CloseIcon />
          {/* <img className="splash-screen-close-button" src={iconClose} alt="" /> */}
        </div>
      </div>
    );
  }
}



